import { AllMarkdownRemark, PostFrontmatter } from "src/utils/types"
import { g, React } from "src/utils/view"

import { CompactPost } from "../post/compact"

type Props = {
  data: {
    allMarkdownRemark: AllMarkdownRemark<PostFrontmatter>
  }
}

export default function Posts({ data }: Props) {
  return (
    <Container>
      {data.allMarkdownRemark.edges.map((edge, idx) => (
        <React.Fragment key={edge.node.frontmatter.url}>
          <CompactPost frontmatter={edge.node.frontmatter} />
          {idx < data.allMarkdownRemark.edges.length && <Gap />}
        </React.Fragment>
      ))}
    </Container>
  )
}

const Container = g("div")({
  display: "flex",
  flexDirection: "column",
  justifyContent: "space-around",
})

const Gap = g("div")({
  flex: "0 0 20px",
})
