import { React } from "src/utils/view"

type Props = {
  children: React.ReactNode
}

export function Random({ children }: Props) {
  const childrenCount = React.Children.count(children)
  if (childrenCount <= 1) return children
  const elementToShow: number = Math.floor(Math.random() * childrenCount)
  return React.Children.toArray(children)[elementToShow]
}
