import GatsbyLink from "gatsby-link"
import { OutboundLink } from "gatsby-plugin-google-analytics"
import { g, React } from "src/utils/view"

type Props = {
  to: string
  newTab?: boolean
  children: React.ReactNode
}

export default function SiteLink({ to, children, newTab }: Props) {
  if (to.substr(0, 1) === "/" && newTab !== true) {
    return (
      <StyledLink activeStyle={{ color: "red" }} to={to}>
        {children}
      </StyledLink>
    )
  } else {
    return (
      <Link href={to} target={newTab === true ? "_blank" : ""}>
        {children}
      </Link>
    )
  }
}

const linkStyle = {
  textDecoration: "none",
  color: "black",
  ":hover": {
    color: "purple",
    outline: "1px solid gray",
    outlineOffset: 2,
  },
  ":focus": {
    outline: "1px dashed gray",
    outlineOffset: 2,
  },
}

const Link = g(OutboundLink)(linkStyle)

const StyledLink = g(GatsbyLink)(linkStyle)
