import { g, React } from "src/utils/view"
import PropTypes from "prop-types"
import Helmet from "react-helmet"
import { StaticQuery, graphql } from "gatsby"

import Header from "src/components/header"
import "./layout.css"
import Emoji from "src/components/emoji"
import ErrorBoundary from "src/components/ErrorBoundary"
import { Random } from "./random"

const Layout = ({ children }) => (
  <ErrorBoundary>
    <StaticQuery
      query={graphql`
        query SiteTitleQuery {
          site {
            siteMetadata {
              title
            }
          }
        }
      `}
      render={data => (
        <>
          <Helmet
            title={data.site.siteMetadata.title}
            meta={[
              { name: "description", content: "nicosommi blog" },
              {
                name: "keywords",
                content: ["blog", "developer", "javascript", "typescript"].join(
                  ", "
                ),
              },
            ]}
          >
            <html lang="en" />
          </Helmet>
          <Header siteTitle={data.site.siteMetadata.title} />
          <Content>{children}</Content>
          <Footer>
            <Random>
              <span>
                Copyright by, guess who? Yeah you guessed it, nicosommi... so
                don't copy, <Emoji char="🐱" label="cat" />
              </span>
              <span>
                Copyright nicosommi. <Emoji char="🤓" label="nerd face" />
              </span>
              <span>
                nicosommi by copyright. <Emoji char="🦀" label="crab" />
              </span>
              <span>
                kikiriki <Emoji char="🐓" label="rooster" />
              </span>
            </Random>
          </Footer>
        </>
      )}
    />
  </ErrorBoundary>
)

const Content = g("div")({
  margin: "5% 20%",
})

const Footer = g("div")({
  display: "flex",
  justifyContent: "flex-end",
  fontSize: "0.75em",
  lineHeight: "2em",
})

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
