// archetype react-error-boundary
// version 0.0.1

import { g, React } from "src/utils/view"

type State = {
  hasError: boolean
  // tslint:disable-next-line no-any
  error?: any
  // tslint:disable-next-line no-any
  info?: any
}

export default class ErrorBoundary extends React.PureComponent<{}, State> {
  public state: State = {
    hasError: false,
  }
  // tslint:disable-next-line no-any
  componentDidCatch(error: any, info: any) {
    if (error) {
      this.setState({ hasError: true, info, error })
    }
  }

  render() {
    const { info, error } = this.state
    if (this.state.hasError) {
      // You can render any custom fallback UI
      return (
        <Container>
          <h1>Oops, an error ocurred</h1>
          <Message>{error.message}</Message>
          <Stacktrace>{info.componentStack}</Stacktrace>
        </Container>
      )
    }
    return this.props.children
  }
}

const Container = g("div")({
  margin: "10%",
})

const Message = g("h2")({})

const Stacktrace = g("div")({
  whiteSpace: "pre-line",
})
