import { g, React } from "src/utils/view"

import SiteLink from "src/components/link"
import { PostFrontmatter } from "src/utils/types"

type Props = {
  frontmatter: PostFrontmatter
}

export function CompactPost({ frontmatter }: Props) {
  return (
    <Container>
      <SiteLink
        to={frontmatter.url}
        newTab={frontmatter.type === "remark-slide"}
      >
        <Header>{frontmatter.title}</Header>
      </SiteLink>
      <Footer>
        {frontmatter.author} - {frontmatter.date}
      </Footer>
    </Container>
  )
}

const Container = g("div")({
  display: "flex",
  flexDirection: "column",
  boxShadow: "inset -5em 3em #EEE",
  padding: 5,
})

const Header = g("h3")({})

const Footer = g("span")({
  textAlign: "left",
  color: "gray",
})
